@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@500&family=Roboto:wght@400;500&family=Ubuntu:ital,wght@0,400;0,500;1,500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  font-family: 'Ubuntu', sans-serif;
}


button{
  padding: 0.6rem 2rem;
  background-color: rgb(1, 119, 119);
  font-size: 16px;
  border-radius: 8px;
  border: none;
}
button:hover{
  background-color: rgb(6, 177, 177);
}



.bold-text{
    font-weight: bold;
}

